<template>
  <Title />
  <Form @ChangeInput="test" @getPhoto="getPhoto" />

  <!-- <div>{{ setWord }}</div> -->

  <Result :PhotoResults="PhotoResults" />

  <!-- <button @click="more">더보기</button> -->

  {{ paging }}
</template>


<script>
import axios from 'axios'
import Title from '@/components/Title.vue'
import Form from '@/components/Form.vue'
import Result from '@/components/Result.vue'


export default {
  name: 'App',
  components: {
    Title,
    Form,
    Result,
  },
  data() {
    return {
      setWord: '',
      resetWord: '',
      PhotoResults: [],
      paging: 1,
      resetPaging: 1,
      flagTag: false,
    }
  },
  methods: {
    test: function (data) {
      console.log(data)
      this.setWord = data
    },
    getPhoto:
      // axios.get('https://api.unsplash.com/search/photos?query=검색어&client_id=액세스키')
      function () {
        this.paging = this.resetPaging
        // console.log(this.setWord)
        axios
          // vite로 생성된 프로젝트는 VITE_로 시작하는 변수만 반영됨.
          .get(`https://api.unsplash.com/search/photos?&query=${this.setWord}&client_id=${process.env.VUE_APP_UNSPLASH_API_KEY}`)
          // 화살표 함수로 접근해야함
          // 안그러면 Uncaught (in promise) TypeError: Cannot set property of undefined 어쩌구 발생.
          .then((res) => {
            // console.log(res.data.result)
            console.log(this.paging + res.data.results)
            this.PhotoResults = res.data.results
          })
      },
    more:
      function () {
        this.paging += 1
        axios
          // vite로 생성된 프로젝트는 VITE_로 시작하는 변수만 반영됨.
          .get(`https://api.unsplash.com/search/photos?page=${this.paging}&query=${this.setWord}&client_id=${process.env.VUE_APP_UNSPLASH_API_KEY}`)
          // 화살표 함수로 접근해야함
          // 안그러면 Uncaught (in promise) TypeError: Cannot set property of undefined 어쩌구 발생.
          .then((res) => {
            // console.log(this.PhotoResults)
            this.PhotoResults.push(...res.data.results)
          })
      },
  },
  created() {
    console.log(this.paging)
  },
  mounted() {
    // Detect when scrolled to bottom.
    const thumbnailContainer = document.querySelector('#app');
    // console.log(listElm.getBoundingClientRect().bottom)
    window.addEventListener('scroll', () => {
      if (this.flagTag == false) {
        if (thumbnailContainer.getBoundingClientRect().bottom < window.innerHeight * 1.3) {

          this.flagTag = true;
          console.log(thumbnailContainer.getBoundingClientRect().bottom)
          //   console.log(window.innerHeight*1.3)
          //   console.log("HELLO")
          this.more()
          this.flagTag = false;
        }
      }
    });
  }

  // setup() {

  //   onMounted(() => {
  //     window.addEventListener("scroll", handleScroll)
  //   })
  //   onUnmounted(() => {
  //     window.removeEventListener("scroll", handleScroll)
  //   })
  //   const handleScroll = (e) => {
  //     if (element.getBoundingClientRect().bottom < window.innerHeight) {
  //       more()
  //     }
  //   }

  // }
}
</script>


